#content{
    background: #3a3535e0;
    padding-top: 0.01rem;
    margin-bottom: 0.2rem;
    padding-bottom: .8rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    border-radius: 8px;
    backdrop-filter: blur(1px);
   

}
#content.dplayer-video dplayer-video-current{
    opacity: 2;
}
[theme=dark] .page.archive{
    background: #3a3535;
    padding-top: 0.1rem;
    margin-bottom: 0.2rem;
    padding-bottom: .8rem;
    padding-left: 0.8rem;
    padding-right: 0.8rem;
    border-radius: 8px;
    opacity: 0.92;
    margin-top: 3.2rem;
}
#toc-static{
    border-radius: 4px;
}

.page.single.special.content {
    text-align: center;
}

#post-footer{
    margin-top: 0;
}
#post-footer.post-info.post-info-line.post-info-mod{
    font-size: 2em;
}

#toc-content-static{
    border-bottom-left-radius: 4px;
    border-bottom-right-radius : 4px;
}
[theme=dark] #toc-static.open .toc-title{
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

@media only screen and (max-width: 680px) {
    .wrapper main .container{
        padding-left: 0.4rem;
        padding-right: 0.4rem;
    }
    [data-header-mobile] .page{
        margin-left: -0rem;
        margin-right: -0rem;
}
}

.dplayer-video-wrap{
    border-radius: 4px;


}
.dplayer *{
    border-radius: 4px;

}


[theme=dark] .single .content h2, 
[theme=dark] .single .content h3, 
[theme=dark] .single .content h4, 
[theme=dark] .single .content h5, 
[theme=dark] .single .content h6{
    background-color: rgba(27, 87, 122, 0.638);
    padding-left: 0.3rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    border-radius: 5px;
    text-shadow: #222 2px 2px 3px;
}

.single .content figure img{
    border-radius: 2px;
}


[theme=dark] .single .content figure figcaption h2, 
[theme=dark] .single .content figure figcaption h3, 
[theme=dark] .single .content figure figcaption h4, 
[theme=dark] .single .content figure figcaption h5, 
[theme=dark] .single .content figure figcaption h6{
    background-color: #3a353557;
    margin-top: 0rem;
    font-size: 13px;
    letter-spacing: 0.1rem;
    padding-top: -1px;
    margin-bottom: 0rem;
}

[theme=dark] .single .content blockquote{
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
}